import {
  AppstoreOutlined,
  ContainerOutlined,
  DesktopOutlined,
  PieChartOutlined,
} from '@ant-design/icons';
import { Menu, BackTop } from 'antd';
import React, { } from 'react';
import { useRoutes, useNavigate } from 'react-router-dom'

import routes from '../../routes'


function getItem(label, key, icon, children, type, onClick) {
  return {
    label,
    key,
    icon,
    children,
    type,
    onClick,
  };
}

const TopMenu = () => {
  const element = useRoutes(routes);
  const navigata = useNavigate();
  const showData = (data) => {
    return () => {
      //console.log(window.location.pathname);  
      if (window.location.pathname !== data) {
        window.scrollTo(0, 0);
      }
      navigata(data);
    }
  }

  const items = [
    getItem('', 'sub0', null, null, 'MenuItemType', showData('/homepage')),
    getItem('', 'sub1', <DesktopOutlined />, [
      getItem('', '1', null, null, 'MenuItemType', showData('/mhw')),
      getItem('', '2', null, null, 'MenuItemType', showData('/dyinglight')),
    ]),
    getItem('MMO/', 'sub2', <ContainerOutlined />, [
      getItem('2', '3', null, null, 'MenuItemType', showData('/diablo2')),
      getItem('3', '4', null, null, 'MenuItemType', showData('/diablo3')),
      getItem('', '5', null, null, 'MenuItemType', showData('/zthj')),
    ]),
    getItem('', 'sub3', <AppstoreOutlined />, [
      getItem('2/', '6', null, null, 'MenuItemType', showData('/red2')),
      getItem('3', '7', null, null, 'MenuItemType', showData('/war3')),
      getItem('', '8', null, null, 'MenuItemType', showData('/7f')),
    ]),
    getItem('', 'sub4', <PieChartOutlined />, [
      getItem('', '9', null, null, 'MenuItemType', showData('/zmxl'))
    ]),
  ];


  return (
    <div className='container'>
      <Menu
        defaultSelectedKeys={['sub0']}
        defaultOpenKeys={['sub0']}
        mode="horizontal"
        items={items}
        className='topmenu'
      />
      <BackTop>
        <div style={{
          height: 60,
          width: 60,
          lineHeight: '60px',
          borderRadius: 30,
          backgroundColor: '#1088e9',
          color: '#fff',
          textAlign: 'center',
          fontSize: 14,
        }} >
          TOP
        </div>
      </BackTop>

      <div id='rightdata'>
        {element}
      </div>
    </div>
  );
};

export default TopMenu;